"use client";

import React from 'react';
import { Package, Calculator, Users, DollarSign } from 'lucide-react';
import Link from 'next/link';

export default function AdminDashboard() {
    const stats = [
        { title: 'สินค้าทั้งหมด', value: '12', icon: <Package size={24} className="text-blue-500" />, bg: 'bg-blue-50' },
        { title: 'คำนวณราคา (วันนี้)', value: '5', icon: <Calculator size={24} className="text-violet-500" />, bg: 'bg-violet-50' },
        { title: 'ผู้เข้าชม (วันนี้)', value: '108', icon: <Users size={24} className="text-orange-500" />, bg: 'bg-orange-50' },
    ];

    return (
        <div>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
                {stats.map((stat, index) => (
                    <div key={index} className="bg-white p-6 rounded-xl shadow-sm border border-gray-100 flex items-center gap-4">
                        <div className={`p-3 rounded-lg ${stat.bg}`}>
                            {stat.icon}
                        </div>
                        <div>
                            <p className="text-gray-500 text-sm">{stat.title}</p>
                            <h3 className="text-2xl font-bold">{stat.value}</h3>
                        </div>
                    </div>
                ))}
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100">
                    <div className="flex justify-between items-center mb-6">
                        <h3 className="text-lg font-bold">เมนูลัด</h3>
                    </div>
                    <div className="grid grid-cols-2 gap-4">
                        <Link href="/admin/products" className="p-4 border border-gray-200 rounded-lg hover:border-black hover:bg-gray-50 transition text-center group">
                            <Package size={32} className="mx-auto mb-2 text-gray-400 group-hover:text-black transition" />
                            <span className="font-medium block">จัดการสินค้า</span>
                        </Link>
                        <Link href="/admin/pricing" className="p-4 border border-gray-200 rounded-lg hover:border-black hover:bg-gray-50 transition text-center group">
                            <Calculator size={32} className="mx-auto mb-2 text-gray-400 group-hover:text-black transition" />
                            <span className="font-medium block">ตั้งค่าราคา</span>
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
}
